import { call, put, fork } from '@redux-saga/core/effects'
import { loginSuccess, loginFailed } from '../slices/auth.slice'
import auth from '../../apis/auth'
import getUserInfoSaga from './getUserInfo.saga'

function* loginSaga(action) {
  console.log('Login: ', action)
  try {
    const { email, password } = action.payload
    const response = yield call(auth.login, { email, password })

    localStorage.setItem('access_token', response.access_token)
    localStorage.setItem('refresh_token', response.refresh_token)

    yield put(loginSuccess(response))

    yield fork(getUserInfoSaga)
  } catch (e) {
    console.log('Login failed: ', e)
    yield put(loginFailed(e))
  }
}

export default loginSaga
