import { connect } from 'react-redux'
import { BrowserRouter as Router, Switch } from 'react-router-dom'
import pt from 'prop-types'
import Navbar from './components/Navbar'
import GuestRoutes from './components/Routes/Guest'
import MemberRoutes from './components/Routes/Members'

function App({ loggedIn, isLoading }) {
  if (isLoading) {
    return <div className="fullscreen__loading">Loading</div>
  }
  return (
    <Router>
      <div>
        <Navbar />
      </div>
      <div>
        <Switch>{!loggedIn ? <GuestRoutes /> : <MemberRoutes />}</Switch>
      </div>
    </Router>
  )
}

App.propTypes = {
  loggedIn: pt.bool,
  isLoading: pt.bool,
}

const mapStoreToProps = (store) => ({
  loggedIn: store.auth.loggedIn,
  isLoading: store.auth.loadingUserData,
})

export default connect(mapStoreToProps)(App)
