import { call, put } from '@redux-saga/core/effects'
import user from '../../apis/user'
import { getUserInfoFailed, getUserInfoSuccess } from '../slices/auth.slice'

function* getUserInfoSaga() {
  try {
    const response = yield call(user.getUserInfo)
    yield put(getUserInfoSuccess(response.data))
  } catch (e) {
    yield put(getUserInfoFailed(e))
  }
}

export default getUserInfoSaga
