import React from 'react'
import { IoMail } from 'react-icons/io5'
import * as yup from 'yup'
import { useCustomForm } from '../../../untils/customHooks/useCustomForm'

const schema = yup
  .object({
    email: yup.string().email().required(),
  })
  .required()

const ForgotPasswordPage = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useCustomForm(schema)

  const onSubmit = (data) => {
    console.log(data)
  }

  return (
    <div className="ForgotPassword__Container">
      <div className="columns is-centered">
        <div className="is-vertically-centered column is-5">
          <div className="container interactive is-flex-direction-column is-justify-content-center is-flex p-6 mt-6 box">
            <h1 className="title is-4 is-horizontaly-centered">
                            Forgot password
            </h1>
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="field">
                <label className="label">
                                    Enter your email
                </label>
                <div className="control has-icons-left">
                  <input
                    className={`input ${
                      errors.email ? 'is-danger' : ''
                    }`}
                    placeholder="Email"
                    {...register('email')}
                  />
                  <span className="icon is-left">
                    <IoMail />
                  </span>
                </div>
                {errors.email && (
                  <p className="help is-danger">
                    {errors.email.message}
                  </p>
                )}
              </div>
              <div className="field">
                <button
                  type="submit"
                  className="is-primary is-rounded is-fullwidth button"
                >
                                    Send email to reset password
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ForgotPasswordPage
