import React from 'react'
import pt from 'prop-types'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import style from './Navbar.module.scss'
import { IoLogIn, IoCheckmarkCircle } from 'react-icons/io5'

const Navbar = ({ loggedIn, user }) => {
  return (
    <nav className={`navbar is-transparent ${style.container}`}>
      <div className="navbar-brand">
        <Link to="/" className="navbar-item is-logo">
          <img src="/logo.svg" alt="People logo" />
        </Link>
        <div
          className="navbar-burger"
          data-target="navbarExampleTransparentExample"
        >
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>

      <div id="navbarExampleTransparentExample" className="navbar-menu">
        <div className="navbar-end">
          {loggedIn ? (
            <div className="navbar-item has-dropdown is-hoverable">
              <a className="navbar-link">
                Hi {`${user.firstName} ${user.lastName}`}
              </a>

              <div className="navbar-dropdown is-right">
                <a className="navbar-item">Profile</a>
                <a className="navbar-item">Setting</a>
                <a className="navbar-item">Support</a>
                <hr className="navbar-divider" />
                <a className="navbar-item">Logout</a>
              </div>
            </div>
          ) : (
            <div className="navbar-item">
              <div className="field is-grouped">
                <p className="control">
                  <Link className="bd-tw-button button" to="/login">
                    <span className="icon">
                      <IoLogIn />
                    </span>
                    <span>Login</span>
                  </Link>
                </p>
                <p className="control">
                  <Link className="button is-primary" to="/register">
                    <span className="icon">
                      <IoCheckmarkCircle />
                    </span>
                    <span>Register</span>
                  </Link>
                </p>
              </div>
            </div>
          )}
        </div>
      </div>
    </nav>
  )
}

Navbar.propTypes = {
  loggedIn: pt.bool,
  user: pt.object,
}

const mapStoreToProps = (store) => ({
  loggedIn: store.auth.loggedIn,
  user: store.auth.user,
})

export default connect(mapStoreToProps)(Navbar)
