import React, { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form';
import { IoMail, IoKey, IoPeople } from 'react-icons/io5'

const RegisterPage = () => {
  const [isSubmit, setIsSubmit] = useState(false);
  const { register, handleSubmit, reset, formState: { errors } } = useForm();
  const onSubmit = data => {
    setIsSubmit(true);
    alert(JSON.stringify(data));
  }

  useEffect(() => {
    setTimeout(() => {
      reset({
        email: '',
        username: '',
        password: ''
      });
      setIsSubmit(false);
    }, 1500);
  }, [reset, isSubmit])

  return (
    <div className="columns is-centered">
      <div className="is-vertically-centered column is-5">
        <div className="container interactive is-flex-direction-column is-justify-content-center is-flex p-6 mt-6 box">
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="field">
              <label className="label">Email <span className="has-text-danger-dark">*</span></label>
              <div className="control has-icons-left">
                <input className="input" type="text" placeholder="Email" name="email" 
                  {...register('email', { 
                    required: true,
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    }
                  })}/>
                <span className="icon is-left">
                  <IoMail />
                </span>
                {errors.email && errors.email.type === 'required' && <span className="has-text-danger-dark">This is required</span>}
                {errors.email && errors.email.type === 'pattern' && <span className="has-text-danger-dark">Invalid email address</span>}
              </div>
            </div>
            <div className="field">
              <label className="label">Username <span className="has-text-danger-dark">*</span></label>
              <div className="control has-icons-left">
                <input 
                  className="input" type="text" placeholder="Username" name="username" {...register('username', { 
                    required: true,
                    minLength: 5, 
                    maxLength: 20
                  })}/>
                <span className="icon is-left">
                  <IoPeople />
                </span>
                {errors.username && errors.username.type === 'required' && <span className="has-text-danger-dark">This is required</span>}
                {errors.username && errors.username.type === 'minLength' && <span className="has-text-danger-dark">Min length exceeded</span> }
                {errors.username && errors.username.type === 'maxLength' && <span className="has-text-danger-dark">Max length exceeded</span> }
              </div>
            </div>
            <div className="field">
              <label className="label">Password <span className="has-text-danger-dark">*</span></label>
              <div className="control has-icons-left">
                <input className="input" type="password" placeholder="Password" name="password" 
                  {...register('password', {
                    required: true,
                  })}
                />
                <span className="icon is-left">
                  <IoKey />
                </span>
              </div>
              {errors.password && errors.password.type === 'required' && <span className="has-text-danger-dark">This is required</span>}
            </div>

            <div className="buttons">
              <button 
                className={`is-primary is-rounded is-fullwidth button ${isSubmit ? 'is-loading' : ''}`}
                tabIndex="0" type="submit"
              >
                                Register
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
};

export default RegisterPage
