import React from 'react'
import { useForm } from 'react-hook-form';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import pt from 'prop-types'
import {login} from '../../store/slices/auth.slice';

const LoginPage = ({loginAction, isLoading}) => {
  const { register, handleSubmit, formState: { errors } } = useForm();
  const onSubmit = ({email, password}) => {
    if (errors.length) {
      return
    }
    loginAction({email, password})
  };

  return (
    // container
    <div className="login__container">
      {/*form*/}
      <form className="login__container__form box" onSubmit={handleSubmit(onSubmit)}>
        {/*header*/}
        <div className="login__container__form__header">
          <span className="is-size-3">LOGIN</span>
          <div className='my-3'/>
        </div>

        {/*content*/}
        <div className="login__container__form__content">
          {/*2 field input*/}
          <div className="field my-4">
            <div className="control">
              <input className={`input ${errors.email && 'is-danger'}`} type="text"
                {...register('email',  { required: true })}
                placeholder="Username" />
            </div>
          </div>
          <div className="field mt-4">
            <div className="control">
              <input className={`input ${errors.password && 'is-danger'}`} type="password"
                {...register('password', { required: true, minLength: 8, maxLength: 60 })}
                placeholder="Password" />
            </div>
          </div>

          {/*remember and forgot*/}
          <div className='is-flex is-justify-content-space-between mb-5'>
            <label className="checkbox is-flex is-align-items-center">
              <input type="checkbox"/>
              &nbsp;Remember me
            </label>
            <Link to='/forgot' className='is-info' >Forgot</Link>
          </div>
        </div>

        <div className="login__container__form__footer">
          {/*button*/}
          <button type="submit" className={
            `button is-primary is-fullwidth is-medium is-info ${isLoading && 'is-loading'}`
          }>
            Sign in</button>
        </div>

      </form>
    </div>
  )

}

LoginPage.propTypes = {
  isLoading: pt.bool,
  loginAction: pt.func
}

const mapStoreToProps = (store) => ({
  isLoading: store.auth.isLoading
})

const actionProps = {
  loginAction: login
}

export default connect(mapStoreToProps, actionProps)(LoginPage)
