import React from 'react';
import { MdPassword } from 'react-icons/md'

import { useLocation } from 'react-router-dom';

import * as Yup from 'yup'
import { useCustomForm } from '../../../untils/customHooks/useCustomForm'

const schema = Yup
  .object({
    password: Yup.string().required('Password is required'),
    passwordConfirmation: Yup.string()
      .oneOf([Yup.ref('password'), null], 'Passwords must match')
  })
  .required()

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

const ResetPasswordPage = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useCustomForm(schema)

  const [state, setState] = React.useState(false);
  const query = useQuery();

  const handleEnter = (e)=>{
    if(e.key==='Enter'){
      handleSubmit(onSubmit);
    }
  }

  const onSubmit = data => {
    console.log(data);
    setState(true);
  };


  return (
    // container
    <div className="resetPassword__container">
      {/*form*/}
      <form className="resetPassword__container__form box" onKeyDown={handleEnter}>
        {/*header*/}
        <div className="resetPassword__container__form__header">
          <span className="is-size-3">LOGIN</span>
          <div className='my-3'/>
        </div>
        {/*content*/}
        <div className="resetPassword__container__form__content">
          {/*hidden field*/}
          <input type="hidden" {...register('token')} defaultValue={query.get('token')}/>
          {/*2 field input*/}
          <div className="field my-4">
            <label className="label">
              Enter your email
            </label>

            <div className="control has-icons-left">
              <input className={`input ${errors.password && 'is-danger'}`} type="password"
                {...register('password')}
                placeholder="" />
              <span className="icon is-left">
                <MdPassword />
              </span>
            </div>
            {errors.password && (
              <p className="help is-danger">
                {errors.password.message}
              </p>
            )}
          </div>
          <div className="field mt-4">
            <div className="control has-icons-left">
              <input className={`input ${errors.passwordConfirmation && 'is-danger'}`} type="password"
                {...register('passwordConfirmation')}
                placeholder="" />
              <span className="icon is-left">
                <MdPassword />
              </span>
            </div>
            {errors.passwordConfirmation && (
              <p className="help is-danger">
                {errors.passwordConfirmation.message}
              </p>
            )}
          </div>
        </div>

        <div className="resetPassword__container__form__footer mt-5">
          {/*button*/}
          <button className={
            `button is-primary is-fullwidth is-medium is-info ${state && 'is-loading'}`
          }
          onClick={handleSubmit(onSubmit)}>
            Sign in</button>
        </div>

      </form>
    </div>
  )
}

export default ResetPasswordPage;
